import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class Footer extends Component {
    render() {
        const cookieStr = "Animasher uses cookies to track who is who. If you don't want a cookie, you can disable them in your browser, but then you will not be able to log in to the site.";
        return (
            <footer>
                <ul>
                    <li><Link to="/">Animations</Link></li>{" - "}
                    <li><Link to="/feedback">Feedback</Link></li>{" - "}
                    <li><Link to="/tos">Terms of Service</Link></li>{" - "}
                    <li><a href="https://creativecommons.org/licenses/by/4.0/" target="_blank" rel="noopener noreferrer">License</a></li>{" - "}
                    <li><button onClick={() => { window.alert(cookieStr); }}>Cookies</button></li>{" - "}
                    <li><Link to="/about">About</Link></li>{" - "}
                    <li><Link to="/sitemap.xml">XML</Link></li>{" - "}
                    <li><Link to="/rules">Rules</Link></li>{" - "}
                    <li><Link to="/parents">For parents</Link></li>{" - "}
                    <li><Link to="/contact">Contact</Link></li>
                </ul>
            </footer>
        );
    }
}
