import React from "react";
import MetaTags from "../MetaTags";

export default function Contact() {
    return (
        <div className="footer-page-static">
            <MetaTags
                title="Contact us"
                description="
                    To contact us about copyright violations, integrity complaints, praise, or anything else, just write to us at inbox@animasher.com.
                    We try to read and reply to everything.
                "
            />

            <h3 className="pinkheader">Contact us - Animasher</h3>
            <p>
                To contact us about copyright violations, integrity complaints, praise, or anything else, just write to us at <a href="mailto:inbox@animasher.com">inbox@animasher.com</a>.
                We try to read and reply to everything.
            </p>
        </div>
    );
}
